import flatpickr from "flatpickr";
import "flatpickr/dist/flatpickr.min.css";
import calendarApiService from "./calendarApiService";

export default function createCalendar() { 
    const input = document.querySelector("#datetime-picker");
    const inputWrapper = document.querySelector(".filters-section-calendar-wrapper")

    const options = {
    enableTime: false,
    time_24hr: true,
    dateFormat: "d/m/Y",
    defaultDate: new Date(),
        minuteIncrement: 1,
        onOpen: [
            function(){
               inputWrapper.classList.add("active") 
            }
        ],
        onClose(selectedDates) {
            const selectedDate = selectedDates[0];
            inputWrapper.classList.remove("active")

            calendarApiService(selectedDate)
                .then(articles => {
                    // 1.        
                    // 2.     ,   
                    articles.map(article => { 
                    //    
                        console.log(article)
                    })

                })
                .catch(error => { 
                    console.log(error)
                    //    (We havent found news from this category),    
                })
        },
    
    };

    return flatpickr(input, options);
}
